import React, { useState, useEffect } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './index.css';

const HeroSection = () => {
    const isMobile = window.innerWidth < 768;
    const initialSettings = {
        dots: isMobile ? false : true,  // No dots on mobile
        infinite: true,
        speed: 2000,
        slidesToShow: isMobile ? 1 : 3,  // Show 1 slide on mobile, 3 otherwise
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 6000,
    };
    const [sliderSettings, setSliderSettings] = useState(initialSettings);

    useEffect(() => {
        const handleResize = () => {
            // Check if mobile inside resize handler
            const isMobile = window.innerWidth < 768;
            setSliderSettings({
                ...sliderSettings,
                dots: !isMobile,  // Toggle dots based on size
                slidesToShow: isMobile ? 1 : 3,  // Toggle number of slides
            });
        };

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    return (
        <section className="Hero">
            <Slider {...sliderSettings}>
                <div>
                    <img src="/sliderimage1.png" alt="Luxury Homes" style={{ width: 'auto', height: 'auto' }} />
                </div>
                <div>
                    <img src="/sliderimage2.png" alt="Fine Art" style={{ width: 'auto', height: 'auto' }} />
                </div>
                <div>
                    <img src="/sliderimage3.png" alt="Luxury Cars" style={{ width: 'auto', height: 'auto' }} />
                </div>
                <div>
                    <img src="/sliderimage4.png" alt="Luxury Boats" style={{ width: 'auto', height: 'auto' }} />
                </div>
            </Slider>
            <h2>Exclusive Insurance Solutions for Discerning Clients</h2>
            <p>Protecting your most valued assets with tailored insurance policies.</p>
            <button onClick={() => console.log('Navigate to quote')}>Get a Quote</button>
        </section>
    );
  };

  export default HeroSection;