import logo from '../../logo.svg';
import './index.css';

const Header = () => {
    return (
      <header className="App-header">
        <img src={logo} alt="Arch Insurance Logo" style={{ width: '150px' }} />
        <nav>
          <ul>
            <li><a href="#services" className="desktop">Services</a></li>
            <li><a href="#about" className="desktop">About Us</a></li>
            <li><a href="#contact" className="desktop">Contact Us</a></li>
            <li><a href="#quote" className="cta-button">Get a Quote</a></li>
          </ul>
        </nav>
      </header>
    );
  };

  export default Header;